import $ from 'jquery';

import AccelerListDispatcher from '../AccelerListDispatcher';
import Constants from '../Constants.jsx';

import Papa from 'papaparse';

const { ActionTypes } = Constants.Inventory;

export default class InventoryActions {
    loadData(url) {
        $.ajax({
            url: url, type: 'GET', success: function (result) {
                let inventoryItems = JSON.parse(result);
                console.log("yep it responded");
                console.log(inventoryItems);
                AccelerListDispatcher.dispatch({
                    type: ActionTypes.LOADED, inventoryItems: inventoryItems,
                });
            }
        });
    }

    handleDataChange(data) {
        AccelerListDispatcher.dispatch({
            type: ActionTypes.UPDATE_DATA, data: data.data, columns: data.meta.fields, totalCount: data.data.length
        });
    }

    updateCSVFile(file) {
        if (file == null) {
            AccelerListDispatcher.dispatch({
                type: ActionTypes.UPDATE_DATA, data: undefined, columns: [], totalCount: 0
            });
            return;
        }
        
        Papa.parse(file, {
            header: true,
            dynamicTyping: true,
            complete: this.handleDataChange.bind(this)
        });
    }

    updateSelectedColumns(columns) {
        AccelerListDispatcher.dispatch({
            type: ActionTypes.UPDATE_SELECTED_COLUMNS, columns: columns
        });
    }
}



