import React, { Component, PropTypes } from 'react';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table';

import InventoryActions from './InventoryActions';
import InventoryStore from './InventoryStore';

import CSVImport from '../csv-import/CSVImport';

// import LabelPrintingModal from '../label-printing-modal/LabelPrintingModal';


export default class Inventory extends Component {
    constructor(props) {
        super(props);

        this.state = {
            items: [],
            inventoryItemStatus: "All",
            dymo: null,
            showLabelPrintingModal: false,
            labelTemplate30252: props.labelTemplate30252,
            labelTemplate30334: props.labelTemplate30334,
            loaded: false,
            lastInventorySync: {},
            channel: props.channel,

            showImportModal: false,
            importStep: 1,
        };

        let selectedPrinter = props.selectedPrinter;
        let labelType = props.labelType;
        let printWhileScanning = props.printWhileScanning;

        if (selectedPrinter == "") {
            this.state.printerData = {
                selectedPrinter: null,
                labelType: null,
            }
            this.state.showLabelPrintingModal = true;
        } else {
            this.state.printerData = {
                selectedPrinter: selectedPrinter,
                labelType: labelType,
                printWhileScanning: printWhileScanning
            }
        }

        dymo.label.framework.trace = 1; //true
        dymo.label.framework.init(this.dymoStartupCode); 
        this.state.dymo = dymo;

        this.actions = new InventoryActions();
        this.store = InventoryStore;
    }
    
    componentDidMount() {
        this.store.addChangeListener(this._onChange);

        this.loadData();
    }

    componentWillMount() {
        this.setState(this.store.getStore());
    }

    componentWillUnmount() {
        this.store.removeChangeListener(this._onChange);
    }

    _onChange = () => {
        this.setState(this.store.getStore());
    }

    /* options */

    openInNewTab(url) {
        let win = window.open(url, '_blank');
        if (win) {
            win.focus();
        }
    }

    productDetailsFormatter = (cell, row) => {
        let a = [];
        const item_name = row[this.state.columns.item_name] ? row[this.state.columns.item_name].slice(0,50) : '';
        const asin = row[this.state.columns.asin] ? row[this.state.columns.asin] : '';
        return (
            <p>
                <small><a href="#a" onClick={this.openInNewTab.bind(this, 'http://www.amazon.com/dp/' + asin)}>{item_name}</a></small><br />ASIN: {asin}
            </p>
        )
    }

    skuFormatter = (cell, row) => {
        console.log('skuformatter', row, cell);
        let status = ''
        if (row[this.state.columns.quantity] > 0) {
            status = (
                <span className="label label-primary" data-toggle="tooltip" data-placement="top" data-original-title="'Active' means that there is at least one sellable item in stock.">Active</span>
            )
        } else {
            status = (
                <span className="label label-warning" data-toggle="tooltip" data-placement="top" data-original-title="'Inactive' means that there are no SELLABLE items in stock. There may or may not be reserved items that have been ordered, but not yet shipped.">Inactive</span>
            )
        }

        return (
            <p>
                {row.seller_sku}
                <br />
                {status}
            </p>
        )
    }

    priceFormatter = (cell, row) => {
        return "$" + cell;
    }

    onRowSelect(row, isSelected){
        console.log(row);
        console.log("selected: " + isSelected)
    }

    onSelectAll(isSelected, currentDisplayAndSelectedData){
        console.log("is select all: " + isSelected);
        console.log("Current select and display data: ");
        console.log(currentDisplayAndSelectedData);
    }

    onAfterSaveCell(row, cellName, cellValue){
        //document.getElementById("send-updates-button").className = "btn btn-sm btn-info margin-right"

        
        // if (cellName == "replenish_qty") {
        // if (isNaN(cellValue) || parseInt(cellValue) < 0) {
        // swal({
        // title: "Invalid Value for Replenishment!",
        // text: "You must enter a valid number representing total number of this SKU to send in to Amazon.",
        // type: "error"
        // }); 
        // }
        // }
    }

    onAfterTableComplete(){
        console.log('Table render complete.');
    }

    onAfterDeleteRow(rowKeys){
        console.log("onAfterDeleteRow");
        console.log(rowKeys);
    }

    onAfterInsertRow(row){
        console.log("onAfterInsertRow");
        console.log(row);
    }

    replenishQtyFormatter(cell, row) {
        if (!cell || cell.trim() == "" || parseInt(cell.trim()) == 0) {
            return (
                <div>0<br /><small>[Click to Edit]</small></div>
            )
        } else if (cell && isNaN(cell) || parseInt(cell) < 0) {
            return (
                <h4><span className="label label-danger" data-toggle="tooltip" data-placement="top" data-html="true" data-original-title="This is not a number. You can't submit this quantity to Amazon!">{cell}</span></h4>
            )
        }
        return (
            <h4><span className="label label-primary" data-toggle="tooltip" data-placement="top" data-html="true" data-original-title="Remember to send your updates to Amazon by clicking the blue button above, after you are finished making changes!">{cell}</span></h4>
        )
    }
    /* options end */

    dymoStartupCode() {
        let printers = dymo.label.framework.getPrinters();
        if (printers.length == 0) {
            throw "No DYMO printers are installed. Install DYMO printers.";
        }
        dymo.label.framework.getPrintersAsync().then(function(printers){
            // Successful result, printers variable has a list of all supported by the DYMO Label Framework
            console.log(printers);
        }).thenCatch(function(error){
          // Error
        });
    }

    setInventoryItemStatus(status) {
        console.log(status);
        this.setState({
            inventoryItemStatus: status
        });
    }

    showLabelPrintingModal() {
        this.setState({showLabelPrintingModal: true})
    }

    closeLabelPrintingModal(selectedPrinter, labelType, printWhileScanning) {
        let printerData = this.state.printerData;
        //console.log("what is the variables?", selectedPrinter, labelType, printWhileScanning)

        printerData.selectedPrinter = selectedPrinter || this.state.printerData.selectedPrinter;
        printerData.labelType = labelType || this.state.printerData.labelType;
        if (printWhileScanning == null) {
            printerData.printWhileScanning = this.state.printerData.printWhileScanning;
        } else {
            if (printWhileScanning == "Yes") {
                printerData.printWhileScanning = true;
            } else {
                printerData.printWhileScanning = false;
            }     
        }

        console.log("new printer data", printerData);

        this.setState({
            printerData: printerData,
            showLabelPrintingModal: false
        });
    }

    // This code block needs refix.
    fulfillmentSKUFormatter = (cell, row) => {
        return (
            <p>
                <button type="button" className="btn btn-sm no-pad btn-primary margin-right" data-toggle="tooltip" data-placement="top" data-original-title={"FNSKU: " + row.fulfillment_channel_sku} onClick={this.printListing(row, 1)}>
                    <i className="pe-7s-print margin-right" ></i><small>Print</small>
                </button>
            </p>
        )
    }

    printListing(listing, quantity) {
        console.log("ok printing", listing, quantity)
        if (this.state.printerData.selectedPrinter == "Don&#39;t use a printer") {
            toastr.error("You have not selected a printer! Please update your printer settings.");
            return;
        }
        let label;
        if (this.state.printerData.labelType == "30252 (dimensions: 1.125 x 3.5)") {
            label = this.state.dymo.label.framework.openLabelXml(this.state.labelTemplate30252);
        } else if (this.state.printerData.labelType == "30334 (dimensions: 2.25 x 1.125)") {
            label = this.state.dymo.label.framework.openLabelXml(this.state.labelTemplate30334);
        }
        let conditionMapping = {'11': 'New', '10': 'Refurbished', '1': 'Used / Like New', '3': 'Used / Good', '2': 'Used / Very Good', '5': 'Collectible / Like New', '4': 'Used / Acceptable', '7': 'Collectible / Good', '6': 'Collectible / Very Good', '9': 'Not Used', '8': 'Collectible / Acceptable'}

        if (label) {
            label.setObjectText("BARCODE", listing.fulfillment_channel_sku || "");
            if (listing.item_name.length > 20) {
                label.setObjectText("TITLE", listing.item_name.slice(0,20) + "...");
            } else {
                label.setObjectText("TITLE", listing.item_name);
            }
            label.setObjectText("CONDITION", conditionMapping[listing.item_condition]);

            if (this.state.printerData.selectedPrinter != null) {
                for (let d=0; d<quantity; d++) {
                    label.print(this.state.printerData.selectedPrinter);
                }
            }
        }
    }

    dateFormatter = (cell, row) => {
        if (cell) {
            return new Date(cell).toDateString();        
        }
        return "N/A";
    }

    supplierFormatter = (cell, row) => {
        if (cell) {
            return cell;        
        }
        return "N/A";
    }

    buyCostFormatter = (cell, row) => {
        if (cell) {
            return "$" + parseFloat(cell).toFixed(2);
        }
        return "$0.00";
    }

    loadData = () => {
        this.actions.loadData('/inventory_v2/' + this.props.channel + '/' + this.state.inventoryItemStatus + '/data/');
    }

    showImportModal() {
        console.log(this.state.columns);
        console.log(this.state.totalCount);
        this.setState({showImportModal: true});
    }

    hideImportModal() {
        this.setState({showImportModal: false, importStep: 1});
        this.actions.updateCSVFile(null);
    }

    setImportStep(step) {
        this.setState({importStep: step});
    }

    render() {
        let table;

        if (!this.state.lastInventorySync) {
            table = (
                <div className="text-center">
                    <hr />
                    <h3>We are still syncing your data!</h3>
                    <br />
                    <div className='loader-1' /><br /><br />
                        <h5>We will send you an email when your data has been synced, so that you can come back to this page.</h5><br />
                        <small>It takes some time to talk to the Amazon and sync your inventory. <br />Hang tight, this may take several hours depending on how large your inventory is. </small>
                    </div>
            )       
        } else if (!this.state.loaded) {
            table = (
                <div className="text-center">
                    <hr />
                    <h3>Loading your inventory... Hang tight!</h3>
                    <br />
                    <div className='loader-1' /><br /><br />
                        <small>If you are a larger seller (more than 25,000 items), it may take some time for this page to load.<br />
                            Once this page is loaded you will be able to search through every one of your items.
                        </small> 
                    </div>
            )
        } else {
            const onRowSelect = (row, isSelected) => {
                console.log(row);
                console.log("selected: " + isSelected)
            };

            const onSelectAll = (isSelected) => {
                console.log("is select all: " + isSelected);
            };
            let selectRowProp = {
                mode: "checkbox",
                clickToSelect: true,
                bgColor: "rgb(238, 193, 213)",
                onSelect: onRowSelect,
                onSelectAll: onSelectAll
            };

            // Validates that the input string is a valid date formatted as "mm/dd/yyyy"
            const isValidFloat = (string) => {
                if (isNaN(parseFloat(string))) {
                    return "You must enter a number!"
                }
                return true;
            };

            const  isValidDate = (dateString) => {
                // First check for the pattern
                if(!/^\d{1,2}\/\d{1,2}\/\d{4}$/.test(dateString))
                    return "That's not a valid date!";

                // Parse the date parts to integers
                let parts = dateString.split("/");
                let day = parseInt(parts[1], 10);
                let month = parseInt(parts[0], 10);
                let year = parseInt(parts[2], 10);

                // Check the ranges of month and year
                if(year < 1000 || year > 3000 || month == 0 || month > 12)
                    return "That's not a valid date!";

                let monthLength = [ 31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31 ];

                // Adjust for leap years
                if(year % 400 == 0 || (year % 100 != 0 && year % 4 == 0))
                    monthLength[1] = 29;

                // Check the range of the day
                if (day > 0 && day <= monthLength[month - 1]) {
                    return true;              
                } else {
                    return "That's not a valid date!";
                }
            };

            const onAfterSaveCell = (row, cellName, cellValue) => {
                console.log('woohoo', row, cellName, cellValue);
                let data = {
                    sku: row.seller_sku,
                    field: cellName,
                    value: cellValue

                }
                $.post('/inventory_v2/set_field/', data, function(result) {
                    if (result != "success") {
                        JSON.parse(result);
                        console.log(result.error)
                    }
                });
            }

            let cellEditProp = {
                mode: "click",
                blurToSave: true,
                afterSaveCell: onAfterSaveCell
            };

            let options = {
                sizePerPage: 10,
                sizePerPageList: [10, 50, 100],
                paginationSize: 10,
                afterTableComplete: this.onAfterTableComplete, // A hook for after table render complete.
                afterDeleteRow: this.onAfterDeleteRow,  // A hook for after droping rows.
                afterInsertRow: this.onAfterInsertRow   // A hook for after insert rows
            };

            if (this.props.channel == "fba") {
                table = (
                    <BootstrapTable data={this.state.items} selectRow={selectRowProp} csvFileName={"Inventory (" + Date() + ")"} exportCSV={true} multiColumnSearch={true} search={true} striped={true} bordered={false} hover={true} pagination={true} cellEdit={cellEditProp} options={options}>
                        <TableHeaderColumn dataSort={true} dataAlign="center" dataField={this.state.columns.item_name} dataFormat={this.productDetailsFormatter}  editable={false}>Product</TableHeaderColumn>
                        <TableHeaderColumn dataAlign="center" dataSort={true} dataField="seller_sku" dataFormat={this.skuFormatter} isKey={true}>SKU</TableHeaderColumn>
                        <TableHeaderColumn dataAlign="center" dataSort={true} dataField={this.state.columns.quantity} editable={false}>Qty</TableHeaderColumn>
                        <TableHeaderColumn dataAlign="center" dataSort={true} dataField={this.state.columns.price} dataFormat={this.priceFormatter} editable={false}>Price</TableHeaderColumn>
                        <TableHeaderColumn dataAlign="center" dataSort={true} editable={ {validator: isValidFloat} } dataFormat={this.buyCostFormatter} dataField={this.state.columns.buy_cost}>Buy Cost</TableHeaderColumn>
                        <TableHeaderColumn dataAlign="center" dataSort={true} editable={true} dataField={this.state.columns.supplier} dataFormat={this.supplierFormatter}>Supplier</TableHeaderColumn>
                        <TableHeaderColumn dataAlign="center" dataSort={true} editable={ {type: "datetime", validator: isValidDate} } dataFormat={this.dateFormatter} dataField={this.state.columns.purchased_date}>Purchased On</TableHeaderColumn>
                        <TableHeaderColumn dataAlign="center" dataField="fulfillment_channel_sku" dataFormat={this.fulfillmentSKUFormatter} editable={false}>Labels</TableHeaderColumn>
                    </BootstrapTable>
                )
            } else {
                table = (
                    <BootstrapTable data={this.state.items} selectRow={selectRowProp} csvFileName={"Inventory (" + Date() + ")"} exportCSV={true} multiColumnSearch={true} search={true} striped={true} bordered={false} hover={true} pagination={true} cellEdit={cellEditProp} options={options}>
                        <TableHeaderColumn dataSort={true} dataAlign="center" dataField={this.state.columns.item_name} dataFormat={this.productDetailsFormatter}  editable={false}>Product</TableHeaderColumn>
                        <TableHeaderColumn dataAlign="center" dataSort={true} dataField="seller_sku" dataFormat={this.skuFormatter} isKey={true}>SKU</TableHeaderColumn>
                        <TableHeaderColumn dataAlign="center" dataSort={true} dataField={this.state.columns.quantity} editable={false}>Qty</TableHeaderColumn>
                        <TableHeaderColumn dataAlign="center" dataSort={true} dataField={this.state.columns.price} dataFormat={this.priceFormatter} editable={false}>Price</TableHeaderColumn>
                        <TableHeaderColumn dataAlign="center" dataSort={true} editable={ {validator: isValidFloat} } dataFormat={this.buyCostFormatter} dataField={this.state.columns.buy_cost}>Buy Cost</TableHeaderColumn>
                        <TableHeaderColumn dataAlign="center" dataSort={true} editable={true} dataField={this.state.columns.supplier} dataFormat={this.supplierFormatter}>Supplier</TableHeaderColumn>
                        <TableHeaderColumn dataAlign="center" dataSort={true} editable={ {type: "datetime", validator: isValidDate} } dataFormat={this.dateFormatter} dataField={this.state.columns.purchased_date}>Purchased On</TableHeaderColumn>
                    </BootstrapTable>
                )
            }
            
        }

        return (
            <div>
                <div className="row">

                    <div className="col-lg-12">
                        <div className="hpanel">
                            <div className="panel-body">

                                <h3 className="font-light m-b-xs">
                                    {this.props.channel} Inventory [BETA]

                                    <span className="label label-default margin-left margin-right"  style={ {"fontSize":"12px", "fontFamily": "'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif", "fontWeight":"400"} }>Last Updated: N/A</span>
                                    <span className="label label-default"  style={ {"fontSize":"12px", "fontFamily": "'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif", "fontWeight":"400"} }>{this.state.items.length} SKUs</span>
                                </h3>
                                <h5></h5>
                                <p>These are the items in your actual Amazon inventory. Here, you can print labels for items.<br />Very soon, you will be able to search for items, modify list price, replenish items and update buy cost/supplier/purchased date for accounting purposes.</p>

                                <br />
                                <div className="dropdown input-sm" style={ {"display": "inline"} }>
                                    <button className="btn btn-default dropdown-toggle" style={ { "padding": "4px 6px"} } type="button" id="menu1" data-toggle="dropdown">
                                        <span className="margin-right"><small>Currently Viewing: {this.state.inventoryItemStatus} Items</small></span>
                                        <span className="caret"></span></button>
                                    <ul className="dropdown-menu" role="menu" aria-labelledby="menu1">
                                        <li role="presentation"><a role="menuitem" href="#" onClick={this.setInventoryItemStatus.bind(null, "All")}>All</a></li>
                                        <li role="presentation"><a role="menuitem" href="#" onClick={this.setInventoryItemStatus.bind(null, "Active")}>Active</a></li>
                                        <li role="presentation"><a role="menuitem" href="#" onClick={this.setInventoryItemStatus.bind(null, "Inactive")}>Inactive</a></li>
                                    </ul>
                                </div>
                                <button className="btn btn-default btn-sm margin-right" onClick={this.showLabelPrintingModal}><i className="pe-7s-print margin-right"></i>Printer Settings</button>

                                <a href="#a" data-toggle="tooltip" data-placement="top" data-html="true" data-original-title="You can use this page to replenish SKUs in your inventory, print labels, or change list prices.<br><br>Click for more details."><u><i className="pe-7s-info margin-right"></i>How do I use this page?</u></a>
                                <button type="button" className="btn btn-info btn-sm" style={{marginLeft: '20px'}} onClick={this.showImportModal.bind(this)}>
                                    <i className="glyphicon glyphicon-import"></i>
                                    <span>Import from CSV</span>
                                </button>
                                <br /><br />
                                {table}

                            </div>
                        </div>
                    </div>
                </div>
                <CSVImport show={this.state.showImportModal} step={this.state.importStep} actions={this.actions} store={this.store} onClose={this.hideImportModal.bind(this)} setImportStep={this.setImportStep.bind(this)} />
            </div>
        );
    }
}

Inventory.propTypes = {
    labelTemplate30252: PropTypes.object.isRequired,
    labelTemplate30334: PropTypes.object.isRequired,
    channel: PropTypes.string.isRequired,
    selectedPrinter: PropTypes.string.isRequired,
    labelType: PropTypes.string.isRequired,
    printWhileScanning: PropTypes.string.isRequired,
};



