import EventEmitter from 'events';

import $ from 'jquery';

import AccelerListDispatcher from '../AccelerListDispatcher';
import Constants from '../Constants.jsx';


const { ActionTypes } = Constants.Inventory;
const INVENTORY_CHANGE = 'inventory_change';

let _store = {
    columns: {
        item_name: 'item_name',
        seller_sku: 'seller_sku',
        buy_cost: 'price_per_unit',
        supplier: 'supplier',
        asin: 'asin',
        purchased_date: 'purchased_date',
        quantity: 'quantity',
        price: 'price',
    },
    loaded: false,
    isCSVLoading: false,
    items: [],
    csv_items: undefined,
    csv_columns: [],
    totalCount: 0
};

class InventoryStore extends EventEmitter {
    constructor() {
        super();
    }

    emitChange() {
        this.emit(INVENTORY_CHANGE);
    }

    addChangeListener(callback) {
        this.on(INVENTORY_CHANGE, callback);
    }

    removeChangeListener(callback) {
        this.removeListener(INVENTORY_CHANGE, callback);
    }

    updateData(data, columns, totalCount) {
        _store.csv_items = data;
        _store.csv_columns = columns;
        _store.totalCount = totalCount;
        _store.isCSVLoading = false;
    }

    updateSelectedColumns(columns) {
        let cols = {};

        cols.seller_sku = _store.csv_columns[parseInt(columns['sl_sku'])];
        cols.buy_cost = _store.csv_columns[parseInt(columns['sl_buy_cost'])];
        if (columns['sl_supplier'] != '')
            cols.supplier = _store.csv_columns[parseInt(columns['sl_supplier'])];
        if (columns['sl_purchased_date'] != '')
            cols.purchased_date = _store.csv_columns[parseInt(columns['sl_purchased_date'])];
        if (columns['sl_item_name'] != '')
            cols.item_name = _store.csv_columns[parseInt(columns['sl_item_name'])];
        if (columns['sl_quantity'] != '')
            cols.quantity = _store.csv_columns[parseInt(columns['sl_quantity'])];
        if (columns['sl_price'] != '')
            cols.price = _store.csv_columns[parseInt(columns['sl_price'])];
        if (columns['sl_asin'] != '')
            cols.asin = _store.csv_columns[parseInt(columns['sl_asin'])];

        let org_items = _store.items.slice(0);

        _store.csv_items.map((csv_item) => {
            let isNew = true;

            org_items.map((item, idx) => {
                if (typeof csv_item[cols.seller_sku] !== 'undefined' && csv_item[cols.seller_sku] == item[_store.columns.seller_sku]) {
                    $.each(cols, (k, v1) => {
                        _store.items[idx][_store.columns[k]] = csv_item[v1];
                    });
                    isNew = false;

                    return;
                }
            });

            if (isNew == true) {
                let new_item = { };

                $.each(cols, (k, v) => {
                    new_item[_store.columns[k]] = csv_item[v];
                });
                _store.items.push(new_item);
            }
        });
        
        org_items.length = 0;
    }

    getStore() {
        return _store;
    }
}

let InventoryStoreInstance = new InventoryStore();

InventoryStoreInstance.dispatchToken = AccelerListDispatcher.register(action => {
    switch (action.type) {
        case ActionTypes.LOADED:
            _store.loaded = true;
            _store.items = action.inventoryItems;
            break;
        case ActionTypes.UPDATE_DATA:
            InventoryStoreInstance.updateData(action.data, action.columns, action.totalCount);
            break;
        case ActionTypes.UPDATE_SELECTED_COLUMNS:
            InventoryStoreInstance.updateSelectedColumns(action.columns);
            break;
        default:
            return true;
    }

    InventoryStoreInstance.emitChange();
});

export default InventoryStoreInstance;


