import React, { Component, PropTypes } from 'react';
import $ from 'jquery';
import { Modal, Button } from 'react-bootstrap';

import DropzoneComponent from 'react-dropzone-component';
import css from './csv_import.css';

import Formsy from 'formsy-react';
import { Select, Row } from 'formsy-react-components';
import Spinner from'react-spinkit';

Formsy.addValidationRule('isNotEqual', (values, value, field) => {
    return value != values[field];
});


export default class CSVImport extends Component {
    constructor(props) {
        super(props);

        this.state = {
            isCSVLoading: false,
        };

        this.djsConfig = {
            autoProcessQueue: false,
            maxFiles: 1,
            acceptedFiles: 'text/csv',
            addRemoveLinks: true,
            previewTemplate: '\
                <div class="dz-preview dz-file-preview">\
                    <div class="dz-image">\
                        <img data-dz-thumbnail />\
                    </div>\
                    <div class="dz-details">\
                        <div class="dz-size" data-dz-size></div>\
                        <div class="dz-filename"><span data-dz-name></span></div>\
                    </div>\
                    <div class="dz-success-mark"><svg width="54px" height="54px" viewBox="0 0 54 54" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" xmlns:sketch="http://www.bohemiancoding.com/sketch/ns">      <title>Check</title>      <defs></defs>      <g id="Page-1" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd" sketch:type="MSPage">        <path d="M23.5,31.8431458 L17.5852419,25.9283877 C16.0248253,24.3679711 13.4910294,24.366835 11.9289322,25.9289322 C10.3700136,27.4878508 10.3665912,30.0234455 11.9283877,31.5852419 L20.4147581,40.0716123 C20.5133999,40.1702541 20.6159315,40.2626649 20.7218615,40.3488435 C22.2835669,41.8725651 24.794234,41.8626202 26.3461564,40.3106978 L43.3106978,23.3461564 C44.8771021,21.7797521 44.8758057,19.2483887 43.3137085,17.6862915 C41.7547899,16.1273729 39.2176035,16.1255422 37.6538436,17.6893022 L23.5,31.8431458 Z M27,53 C41.3594035,53 53,41.3594035 53,27 C53,12.6405965 41.3594035,1 27,1 C12.6405965,1 1,12.6405965 1,27 C1,41.3594035 12.6405965,53 27,53 Z" id="Oval-2" stroke-opacity="0.198794158" stroke="#747474" fill-opacity="0.816519475" fill="#FFFFFF" sketch:type="MSShapeGroup"></path>      </g>    </svg>\
                    </div>\
                    <div class="dz-error-mark">\
                        <svg width="54px" height="54px" viewBox="0 0 54 54" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" xmlns:sketch="http://www.bohemiancoding.com/sketch/ns">      <title>Error</title>      <defs></defs>      <g id="Page-1" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd" sketch:type="MSPage">        <g id="Check-+-Oval-2" sketch:type="MSLayerGroup" stroke="#747474" stroke-opacity="0.198794158" fill="#FFFFFF" fill-opacity="0.816519475">          <path d="M32.6568542,29 L38.3106978,23.3461564 C39.8771021,21.7797521 39.8758057,19.2483887 38.3137085,17.6862915 C36.7547899,16.1273729 34.2176035,16.1255422 32.6538436,17.6893022 L27,23.3431458 L21.3461564,17.6893022 C19.7823965,16.1255422 17.2452101,16.1273729 15.6862915,17.6862915 C14.1241943,19.2483887 14.1228979,21.7797521 15.6893022,23.3461564 L21.3431458,29 L15.6893022,34.6538436 C14.1228979,36.2202479 14.1241943,38.7516113 15.6862915,40.3137085 C17.2452101,41.8726271 19.7823965,41.8744578 21.3461564,40.3106978 L27,34.6568542 L32.6538436,40.3106978 C34.2176035,41.8744578 36.7547899,41.8726271 38.3137085,40.3137085 C39.8758057,38.7516113 39.8771021,36.2202479 38.3106978,34.6538436 L32.6568542,29 Z M27,53 C41.3594035,53 53,41.3594035 53,27 C53,12.6405965 41.3594035,1 27,1 C12.6405965,1 1,12.6405965 1,27 C1,41.3594035 12.6405965,53 27,53 Z" id="Oval-2" sketch:type="MSShapeGroup"></path>        </g>      </g>    </svg>\
                    </div>\
                    <div class="dz-error-message"><span data-dz-errormessage></span></div>\
                </div>\
            ',
        };

        this.componentConfig = {
            postUrl: 'no-url',
            iconFiletypes: ['.csv'],
            showFiletypeIcon: false,
        };

        this.dropzone = null;

        this.actions = props.actions;
        this.store = props.store;
    }
    
    componentDidMount() {
        this.store.addChangeListener(this._onChange);
    }

    componentWillMount() {
        this.setState(this.store.getStore());
    }

    componentWillUnmount() {
        this.store.removeChangeListener(this._onChange);
    }

    _onChange = () => {
        this.setState(this.store.getStore());
    }

    handleClose() {
        this.dropzone = null;
        this.props.onClose();
    }

    handleNext() {
        if (this.dropzone && this.dropzone.files.length && this.props.step == 1) {
            this.props.setImportStep(2);
        } else if (this.props.step == 2) {
            this.refs.form.submit();
        }
    }

    handleSelectColsFormSubmit(model, reset, error) {
        this.actions.updateSelectedColumns(model);
        this.props.setImportStep(3);
    }

    getEditDistance(a, b) {
        if (a.length === 0) return b.length; 
        if (b.length === 0) return a.length; 

        var matrix = [];

        // increment along the first column of each row
        var i;
        for (i = 0; i <= b.length; i++) {
            matrix[i] = [i];
        }

        // increment each column in the first row
        var j;
        for (j = 0; j <= a.length; j++) {
            matrix[0][j] = j;
        }

        // Fill in the rest of the matrix
        for (i = 1; i <= b.length; i++) {
            for (j = 1; j <= a.length; j++) {
                if (b.charAt(i-1) == a.charAt(j-1)) {
                    matrix[i][j] = matrix[i-1][j-1];
                } else {
                    matrix[i][j] = Math.min(matrix[i-1][j-1] + 1, // substitution
                        Math.min(matrix[i][j-1] + 1, // insertion
                        matrix[i-1][j] + 1)); // deletion
                }
            }
        }

        return matrix[b.length][a.length];
    }

    removeItemFromArray(arr, name, value){
        let array = $.map(arr, (v) => {
            return v[name] === value ? null : v;
        });

        arr.length = 0; //clear original array
        arr.push.apply(arr, array); //push all elements except the one we want to delete

        return arr;
    }

    getSimilarFieldNames(backend_cols, csv_cols) {
        let cols = csv_cols.map((v, i) => {
            return ({name: v, idx: i});
        });
        let rs = {};

        $.each(backend_cols, (k, v1) => {
            let similar_item = {
                column_name: k,
                csv_column_idx: null,
                distance: null
            };

            cols.map((v2) => {
                const dt = this.getEditDistance(v1, v2.name);

                if (similar_item.distance == null || similar_item.distance > dt) {
                    similar_item.column_name = k;
                    similar_item.csv_column_idx = v2.idx;
                    similar_item.distance = dt;
                }
            });

            cols = this.removeItemFromArray(cols, 'idx', similar_item.csv_column_idx);
            
            rs[similar_item.column_name] = {
                csv_column_idx: similar_item.csv_column_idx
            }
        });

        return rs;
    }

    getCSVImportingModal = (step) => {
        if (step == 1) {
            const componentConfig = this.componentConfig;
            const djsConfig = this.djsConfig;

            // For a list of all possible events (there are many), see README.md!
            const eventHandlers = {
                init: this.handleInitDropzone.bind(this),
                addedfile: this.handleFileAdded.bind(this),
                removedfile: this.handleFileRemoved.bind(this),
                maxfilesexceeded: this.handleMaxFilesExceeded.bind(this),
            }

            return (
                <div>
                    <h4>Select a CSV file to import</h4>
                    <p>Your CSV file should contain at least 2 columns which will correspond to an expense MSKU and cost.</p>
                    <DropzoneComponent config={componentConfig}
                           eventHandlers={eventHandlers}
                           djsConfig={djsConfig} />
                </div>
            );
        } else if (step == 2) {
            let columns = [{value: '', label: 'Select'}];
            this.state.csv_columns.map((val, index) => {
                columns.push({value: index, label: val});
            });

            let similar_items = this.getSimilarFieldNames(this.state.columns, this.state.csv_columns);

            return (
                <div>
                    <h5>Select the corresponding columns from your CSV</h5>
                    <Formsy.Form ref="form"
                            className="form-horizontal"
                            onValidSubmit={this.handleSelectColsFormSubmit.bind(this)}>

                        <Select name="sl_sku"
                                label="SKU"
                                labelClassName={""}
                                elementWrapperClassName={"col-sm-8"}
                                value={similar_items['seller_sku'].csv_column_idx}
                                options={columns}
                                required
                                validationErrors={{isDefaultRequiredValue: 'This field should not be empty.'}} />
                        <Select name="sl_buy_cost"
                                label="BUY COST"
                                labelClassName={""}
                                elementWrapperClassName={"col-sm-8"}
                                value={similar_items['buy_cost'].csv_column_idx}
                                options={columns}
                                required
                                validations="isNotEqual:sl_sku"
                                validationErrors={{
                                    isDefaultRequiredValue: 'This field should not be empty.',
                                    isNotEqual: 'This field should be different from the SKU field.'}} />
                        <hr />
                        <h5>Optional Fields</h5>
                        <Select name="sl_item_name"
                                label="PRODUCT NAME"
                                labelClassName={""}
                                elementWrapperClassName={"col-sm-8"}
                                value={''}
                                options={columns}
                                 />
                        <Select name="sl_supplier"
                                label="SUPPLIER"
                                labelClassName={""}
                                elementWrapperClassName={"col-sm-8"}
                                value={similar_items['supplier'].csv_column_idx}
                                options={columns}
                                />
                        <Select name="sl_purchased_date"
                                label="PURCHASED ON"
                                labelClassName={""}
                                elementWrapperClassName={"col-sm-8"}
                                value={similar_items['purchased_date'].csv_column_idx}
                                options={columns}
                        />
                        <Select name="sl_quantity"
                                label="QUANTITY"
                                labelClassName={""}
                                elementWrapperClassName={"col-sm-8"}
                                value={similar_items['quantity'].csv_column_idx}
                                options={columns}
                         />
                        <Select name="sl_price"
                                label="PRICE"
                                labelClassName={""}
                                elementWrapperClassName={"col-sm-8"}
                                value={similar_items['price'].csv_column_idx}
                                options={columns}
                        />
                        <Select name="sl_asin"
                                label="ASIN"
                                labelClassName={""}
                                elementWrapperClassName={"col-sm-8"}
                                value={similar_items['asin'].csv_column_idx}
                                options={columns}
                        />
                    </Formsy.Form>
                </div>
            );
        } else if (step == 3) {
            return (
                <div>
                    <div className="alert alert-success">
                        <strong>Success!</strong> The import was successful.
                    </div>
                </div>
            );
        } else {
            return (
                <div>
                    <div className="alert alert-danger">
                        <strong>Error!</strong> Something went wrong!
                    </div>
                </div>
            );
        }
    }

    getStepButtons = () => {
        const isLoading = this.state.isCSVLoading;
        const spinner = isLoading ? <Spinner spinnerName='circle' className="loading-spinner" /> : "";

        if (this.props.step == 1) {
            const canMove = typeof this.state.csv_items !== 'undefined';

            return (
                <div>
                    <Button bsStyle="primary" onClick={!isLoading ? this.handleClose.bind(this) : null} disabled={isLoading}>Cancel</Button>
                    {canMove ? <Button bsStyle="primary" onClick={this.handleNext.bind(this)}>Next</Button> : <Button bsStyle="primary" disabled onClick={null}>Next</Button> }
                    {spinner}
                </div>
            );
        } else if (this.props.step == 2) {
            const canMove = typeof this.state.csv_items !== 'undefined';

            return (
                <div>
                    <Button bsStyle="primary" onClick={this.handleClose.bind(this)}>Cancel</Button>
                    {canMove ? <Button bsStyle="primary" onClick={this.handleNext.bind(this)}>Next</Button> : <Button bsStyle="primary" disabled onClick={null}>Next</Button> }
                    {spinner}
                </div>
            );
        }

        return (
            <div>
                <Button bsStyle="primary" onClick={this.handleClose.bind(this)}>Close</Button>
            </div>
        );
    }

    /* Dropzone Callbacks */
    handleFileAdded(file) {
        if (this.dropzone.files.length > 1) {
            this.dropzone.removeFile(file);
        } else {
            this.setState({isCSVLoading: true});
            this.actions.updateCSVFile(file);
        }
    }

    handleFileRemoved(file) {
        if (this.dropzone.files.length == 0) {
            this.actions.updateCSVFile(null);
        }
    }

    handleInitDropzone(dz) {
        this.dropzone = dz;
    }

    handleMaxFilesExceeded(file) {
        alert("You are allowed to upload only one CSV file!");
    }

    render() {
        const modalInstance = (
            <div className="csv-import-modal">
                <Modal show={this.props.show}>
                    <Modal.Header>
                        <Modal.Title className="text-center">Import Inventory Data</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        {this.getCSVImportingModal(this.props.step)}
                    </Modal.Body>
                    <Modal.Footer style={{textAlign: 'center'}}>
                        {this.getStepButtons()}
                    </Modal.Footer>
                </Modal>
            </div>
        );
        return (
            <div className="csv-import-section">
                {modalInstance}
            </div>
        );
    }
}

CSVImport.propTypes = {
    actions: PropTypes.object.isRequired,
    store: PropTypes.object.isRequired,
    onClose: PropTypes.func.isRequired,
    setImportStep: PropTypes.func.isRequired,
    step: PropTypes.number.isRequired,
    show: PropTypes.bool.isRequired
};



